*{
    box-sizing: border-box;
}
.popup-container{
    position: fixed;
    margin:auto;
   top:50%;
   left:50%;
   transform:translate(-50%,-50%);
    min-width:30%;
    background-color: #a3c3f1;
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-around; 
    text-align: center;   
    
}
.popup-box{
    width:400px;
    position: relative;
    padding: 32px;
    width:100%;
    max-width: 640px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;   
}
.popup-box input{
    margin-bottom: 20px;
    
}

.popup-box input,button{
    width:100%;
    padding: 10px;
    box-sizing: border-box;
    border-radius: 5px;
    outline: none;
    border: none;
    background-color: #1f519c;
    box-shadow: inset -3px -3px 5px #ffffff70, inset 3px 3px 15px #00000070;
    color:gray;

}

.popup-box button{
    cursor: pointer;
}
.popup-box button:hover{
    background-color:#2E5478;
}
.close-button i{
    color:#2E5478;
    cursor: pointer;
    padding: 10px;   
}
@media screen and (max-width:600px){
    .popup-container{
        margin: auto;
        width: 300px;
        
    }
}
